@import url(https://fonts.googleapis.com/css2?family=Anton&family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AppBG {
    background-image: url("https://img5.goodfon.ru/wallpaper/nbig/c/25/tekstura-fon-uzor-retro-vintazh-chernyi-seryi.jpg");
    background-repeat: repeat ;
    height: 3000px;
    background-attachment: fixed  ;
}

.app-wrapper {
    margin: 0 auto;
    height: 1000px;
    width: 1200px;
    display: grid;
    grid-template-areas:
      'h h '
      'n c';
    grid-template-rows: 120px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-template: 'c';
    background: rgba(255, 255, 255, 0.5);
    border: white 1px solid;
    border-radius: 7px;
    color: white;
    font-size: 20px;
}



